import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';

Vue.config.productionTip = false;

// @ts-ignore
let _hmt: any = _hmt || [];
// @ts-ignore
window._hmt = _hmt;
(function () {
  let hm = document.createElement('script');
  hm.src = 'https://hm.baidu.com/hm.js?90f91279aefecacc85a079459166ee29';
  let s: HTMLElement = document.getElementsByTagName('script')[0];
  // @ts-ignore
  s.parentNode.insertBefore(hm, s);
})();

router.beforeEach((to, from, next) => {
  if (_hmt) {
    if (to.path) {
      _hmt.push(['_trackPageview', '/#' + to.fullPath]);
    }
  }
  next();
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
